.full-service-section {
  padding: 100px 0px;
  background-color: $clr2;
  margin-bottom: 80px;

  .full-service-container {
    h1 {
        text-align: center;
        margin-bottom: 70px;
      color: $clr1;
    }
    max-width: 1140px;
    margin: auto;
    .services {
      display: grid;
      grid-template-columns: repeat(auto-fill, minmax(350px, 1fr));
      grid-row-gap: 35px;
      grid-column-gap: 29px;
    }
  }
}
